import React, { Component } from 'react';
import styled from 'styled-components';

export default class News extends Component {
  constructor(props){
    super(props);
    this.state = {
      news: [],
      comments: [],
    }

    this.countComments = this.countComments.bind(this);
  }

  componentDidMount() {
    fetch(`http://akvatory.local/api/news/read.php`)
      .then(response => response.json())
      .then(news => this.setState({ news: news.records }))
    fetch(`http://akvatory.local/api/news/getcomments.php`)
      .then(response => response.json())
      .then(comments => this.setState({ comments: comments.records }))        
  }

  // считает кол-во комментариев
  countComments = (id) => (this.state.comments.filter((comment) => comment.news_id === id)).length;
 
  render() {
    const { news, comments } = this.state;

    return (
      <div>
        {news.map((item,i) => {  
          return (
            <NewsContainer key={i}> 
              <UserAvatar src={window.location.origin + `/img/photos/images.png`} /> 
              <NewsInfo>
                <p><strong>{item.author}</strong> {item.date}</p>
              </NewsInfo>
              <NewsTitle>{item.title}</NewsTitle>
              <div dangerouslySetInnerHTML={{__html: item.text}}></div>
              <Actions>
                <p>{this.countComments(item.id)}</p> <Icon src={window.location.origin + `/img/icons/comment.png`} /> 
                <p>{item.likes}</p> <Icon onClick={() => alert(item.id)} src={window.location.origin + `/img/icons/comment.png`} />  
                <Icon src={window.location.origin + `/img/icons/comment.png`} />    
              </Actions>
            </NewsContainer> 
            )
          }
       )}  
      </div>
    )
  }
}

const NewsContainer = styled.div`
  position: relative;
  padding: 20px 20px 20px 140px;
  cursor: pointer;
  border-bottom: 1px solid #e6ecf0;

  :hover {
    background: #e6f7ff;
    transition: .37s ease;
  }
`;

const NewsTitle = styled.p`
  font-size: 1.37rem;
`;

const NewsInfo = styled.div`
  p {
    color: #657786;
  }
  strong {
    color: #000;
    :after {
      content: '\00b7';
      margin-left: 5px;
    }
  }
`;

const UserAvatar = styled.img`
  position: absolute;
  left: 25px;
  top: 37px;
  border-radius: 50%;
  width: 80px;
`;

const Icon = styled.img`
  opacity: .75;
  width: 15px;
  height: 15px; 
  margin-right: 50px;

  :hover {
    opacity: 1;
    transition: .37s ease;
  }
`;

const Actions = styled.div`
  display: flex;
  align-items: center;

  p {
    margin-right: 5px;
    color: #657786;
  }
`;